.loader {
  height: 32px;
  width: 150px;
  box-sizing: border-box;
  border: 2px solid #fff;
  border-radius: 20px;
  display: block;
  position: relative;
  overflow: hidden;
}

.loader:before {
  content: "";
  width: 24px;
  height: 24px;
  background: #ff3d00;
  border-radius: 50%;
  animation: ballbns 3s ease-in-out infinite;
  position: absolute;
  bottom: 2px;
  left: 0;
}

@keyframes ballbns {
  0% {
    left: 0;
    transform: translateX(0%);
    box-shadow: -5px 0 0 -1px rgba(255, 61, 0, .9), -10px 0 0 -2px rgba(255, 61, 0, .8), -15px 0 0 -4px rgba(255, 61, 0, .6), -20px 0 0 -6px rgba(255, 61, 0, .4), -25px 0 0 -8px rgba(255, 61, 0, .2);
  }

  49% {
    left: 100%;
    transform: translateX(-100%);
    box-shadow: -5px 0 0 -1px rgba(255, 61, 0, .9), -10px 0 0 -2px rgba(255, 61, 0, .8), -15px 0 0 -4px rgba(255, 61, 0, .6), -20px 0 0 -6px rgba(255, 61, 0, .4), -25px 0 0 -8px rgba(255, 61, 0, .2);
  }

  51% {
    left: 100%;
    transform: translateX(-100%);
    box-shadow: 5px 0 0 -1px rgba(255, 61, 0, .9), 10px 0 0 -2px rgba(255, 61, 0, .8), 15px 0 0 -4px rgba(255, 61, 0, .6), 20px 0 0 -6px rgba(255, 61, 0, .4), 25px 0 0 -8px rgba(255, 61, 0, .2);
  }

  100% {
    left: 0;
    transform: translateX(0%);
    box-shadow: 5px 0 0 -1px rgba(255, 61, 0, .9), 10px 0 0 -2px rgba(255, 61, 0, .8), 15px 0 0 -4px rgba(255, 61, 0, .6), 20px 0 0 -6px rgba(255, 61, 0, .4), 25px 0 0 -8px rgba(255, 61, 0, .2);
  }
}

.error {
  color: red;
  font-size: 20px;
  display: none;
}

.visible {
  display: block;
}

.description, .temperament {
  font-size: 20px;
}

.wrapper {
  max-width: 500px;
  flex-direction: column;
  align-items: flex-start;
  margin: 10px;
  display: flex;
}

.cat-info {
  display: flex;
}

.breed-select {
  margin: 10px;
  font-size: 20px;
  display: none;
}

.title {
  margin: 0 auto;
}

